<template>
  <section>
    <v-container fluid>
      <v-row>
        <v-col md="12">
          <PageHeader
            :title="headercontent.title"
            :addlink="headercontent.addlink"
          />
        </v-col>
        <v-col md="12" v-if="false">
          <h3 class="filtertitle">Filter {{ headercontent.title }}</h3>
          <v-form
            ref="form"
            v-model="valid"
            lazy-validation
            class="form-section"
          >
            <v-row>
              <v-col cols="3">
                <v-text-field
                  v-model="title"
                  outlined
                  label="Subcategory Titlte"
                  class="mr-btm-minus"
                ></v-text-field>
              </v-col>
              <v-col cols="12" class="submitbtn">
                <v-btn
                  :disabled="!valid"
                  class="submitbutton btn-dark"
                  @click="validate"
                >
                  Filter {{ headercontent.title }}
                </v-btn>
                <!-- <v-progress-circular
         
                  indeterminate
                ></v-progress-circular> -->
              </v-col>
            </v-row>
          </v-form>
        </v-col>
        <v-col md="12">
          <v-simple-table class="table" v-if="!loading">
            <template v-slot:default v-if="allPrompters.results">
              <thead>
                <tr>
                  <th class="text-left">S/N</th>
                  <th class="text-left">Message</th>
                  <th class="text-left">Emotion</th>

                  <th class="text-right" colspan="4">Action</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in allPrompters.results" :key="index">
                  <td>{{ index + 1 }}</td>
                  <td class="text-capitalize">{{ item.message }}</td>
                  <td class="text-capitalize">{{ item.connectedTo }}</td>
                  <td>
                    <img
                      :src="'http://localhost:3002' + item.imageUrl"
                      alt=""
                    />
                  </td>
                  <td class="text-right">
                    <router-link
                      :to="`/dashboard/prompter/add/${item._id}`"
                    >
                      <v-icon class="icons-action"> mdi-pencil </v-icon>
                    </router-link>

                    <v-icon
                      class="icons-action"
                      @click="deletePrompoterAct(item._id)"
                    >
                      mdi-delete
                    </v-icon>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <div v-if="loading">
            <v-skeleton-loader type="image" height="200"></v-skeleton-loader>
            <v-skeleton-loader type="image" height="200"></v-skeleton-loader>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>
import PageHeader from "@/components/dashboard/PageHeader";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Subemotion",
  components: { PageHeader },
  data: () => ({
    headercontent: {
      title: "Prompter",
      addlink: "/dashboard/prompter/add",
    },
    title: "",
    adsposition: "",
    valid: true,
    limit: 100,
    skip: 1,
    loading: true,
  }),
  methods: {
    validate() {},

    ...mapActions(["getPrompters", "deletePrompter"]),
    async deletePrompoterAct(id) {
      confirm("Are you sure you want to delete this prompter?") &&
        (await this.deletePrompter(id));
    },
  },
  async created() {
    await this.getPrompters({ limit: this.limit, skip: this.skip });
    this.loading = false;
  },
  computed: {
    ...mapGetters(["allPrompters"]),
  },
};
</script>

